/* phone */
@media all and (max-width: 767px) { 
	body {
		--bio_text_size: 18px
	}

	#bio-content {
	}
}

/* others */
@media all and (min-width: 768px) {
	body {
		--bio_text_size: 36px
	}

	#bio-content {
		grid-column-start: 4;
		grid-column-end: 13;
	}
}

.bio_text_1 {
	font-family: 'Noto Serif KR';
	font-weight: 300;
	font-size: var(--bio_text_size);
	color: #20252E;
}

.bio_text_2 {
	font-family: 'Noto Serif KR';
	font-weight: 500;
	font-size: var(--bio_text_size);
	border-bottom: 2px solid #20252E;
	color: #20252E;
}
